<template>
  <section class="container">
    <!-- 大图导航栏 -->
    <div class="bgPicture">
      <el-row :gutter="10">
        <el-col :offset="2" :xs="22" :sm="22" :md="14" :lg="14" :xl="14">
          <div class="introduce">
            <h3 class="title">一站式企业内训服务商</h3>
            <p class="desc">专注职场培训，通过线上线下结合的方式，让企业培训更简单，员工学习更有效</p>
            <ul class="speciality">
              <li> “丰富实战经验+科学方法论”的资深讲师</li>
              <li>一对一专属培训顾问，提供培训解决方案</li>
              <li>2000节+线上精品课程</li>
            </ul>
          </div>
        </el-col>
        <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8">
          <div class="contact df dfdir dfaic">
            <p class="limit">联系我们</p>
            <p class="phone">1523212532</p>
            <button class="findBusiness">找企业内训</button>
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 合作企业 -->
    <div class="cooperation">
        <h3>截止目前，我们已累计服务<span class="sign">23212</span>家企业，覆盖<span class="sign">40万+</span>多职场人</h3>
        <div class="swiperAuto">
          <swiper
            :options="swiperOption"
            ref="mySwiper"
            class="swiperCon"
            @slideChange="slideChange"
          >
            <swiper-slide v-for="(items,index) in businessList" :key="items+index">
                <div class="slide-box">
                    <el-row :gutter="10">
                        <el-col
                            class="company"
                            v-for="(business, num) in items" :key="business+num"
                            :xs="12" :sm="8" :md="8" :lg="4" :xl="4">
                            <div class="df dfc">
                                <img class="icon" src="#"/>
                                <span class="name">{{business.name}}</span>
                            </div>
                        </el-col>
                    </el-row>
              </div>
            </swiper-slide>
          </swiper>
            <!-- 切换按钮 -->
            <div :class="currentSwiperIndex === 0 ? 'swiper-btn-prev btn-disabled': 'swiper-btn-prev'">
                <span class="iconfont prev"></span>
            </div>
            <div :class="currentSwiperIndex === businessList.length-1? 'swiper-btn-next btn-disabled':'swiper-btn-next'">
                <span class="iconfont next"></span>
            </div>
        </div>
    </div>
    <!-- 内训讲师 -->
    <div class="mode teachers" v-if="teacher.data && teacher.data.data">
      <div class="top df dfjbw dfaic">
        <h3 class="title">内训讲师<span class="subTitle">为您提供优质的培训方案</span></h3>
        <!-- <span class="more">更多课程</span> -->
      </div>
      <div class="lecturer">
        <el-row :gutter="24">
          <el-col
            :xs="24" :sm="24" :md="12" :lg="6" :xl="6"
            v-for="(t, index) in teacher.data.data" :key="t + index"
          >
            <lecturer
              :teacherInfo="t"
              @click.native="seeTeacherDetail(t)" />
          </el-col>
        </el-row>
      </div>
    </div>

    <!-- 线下课 -->
    <div class="mode classes" v-if="courseList.data && courseList.data.list">
      <div class="top df dfjbw dfaic">
        <h3 class="title">线下课<span class="subTitle">精品课任你挑选</span></h3>
        <!-- <span class="more">更多课程</span> -->
      </div>
      <div class="course">
        <el-row :gutter="20">
          <el-col
            :xs="24" :sm="24" :md="12" :lg="8" :xl="8"
            v-for="(course, index) in courseList.data.list" :key="course + index"
          >
            <course
              :courseInfo="course"
              @click.native="seeCourseDetail(course)"
              @changeCollectStatus="changeCollectStatus"
              @changeHandleReport="changeHandleReport"
            />
          </el-col>
        </el-row>
      </div>
    </div>

    <!-- 提交信息 -->
    <div class="subInfo">
      <p class="tips">如有任何企业培训需求，请留言说明，我们将为您提供更多方案建议</p>
      <div class="df dfc selectRadio">
        <el-radio v-model="requestParams.consultType" label="1" style="margin-right: 50px;"><span class="radioName">企业咨询</span></el-radio>
        <el-radio v-model="requestParams.consultType" label="2"><span class="radioName">个人咨询</span></el-radio>
      </div>
      <div class="competeInfo">
        <el-row :gutter="15">
          <el-col :xs="24" :sm="24" :md="16" :lg="16" :xl="16" class="df">
            <input type="text" ref="companyName" v-model="requestParams.companyName" class="companyName df1" placeholder="企业名称"/>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df">
              <el-select
                class="industry df1"
                v-model="requestParams.industry"
                placeholder="请选择行业"
              >
                <el-option
                  v-for="ind in industryList"
                  :key="ind.value"
                  :label="ind.label"
                  :value="ind.value">
                </el-option>
              </el-select>
          </el-col>
        </el-row>
        <el-row :gutter="15">
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df">
            <input type="text" ref="name" v-model="requestParams.name" class="name df1" placeholder="姓名"/>
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df">
            <input type="text" maxLength='11'
              ref="mobile"
              v-model="requestParams.mobile"
              class="phone df1"
              placeholder="手机号"
              oninput = "value=value.replace(/[^\d]/g,'')"
            />
          </el-col>
          <el-col :xs="24" :sm="24" :md="8" :lg="8" :xl="8" class="df">
            <input type="text" ref="job" v-model="requestParams.job" class="position df1" placeholder="职位"/>
          </el-col>
        </el-row>
        <el-row :gutter="15">
          <el-col :span="24" class="df">
            <textarea rows="6" v-model="requestParams.content" class="courseNeed df1" style="resize:none;" placeholder="课程需求"/>
          </el-col>
        </el-row>
      </div>
      <button class="submitInfo" @click="submitData">提交</button>
    </div>

    <div class="Modular"></div>
    <!-- 注册领取福利 -->
    <div class="getGift df dfjbw dfaic" v-if="false">
      <div class="df dfaic">
        <img src="#" class="giftPic">
        <div class="sign">
          <p>注册领福利</p>
          <span>线上2102节课程免费畅享·1对1专属顾问服务</span>
        </div>
      </div>
      <div class="df dfaic">
        <p class="haveGift"><span class="">12232</span>家企业已领取</p>
        <button class="freeGet">立即免费领取</button>
      </div>
    </div>
    <PaymentPopup v-if="showPayPopup" :courseId="courseId" :from="from" @close="showPayPopup=false"/>
  </section>
</template>
<script>
  import {mapState, mapMutations,mapActions} from 'vuex'
  import Lecturer from '../../components/Lecturer'
  import Course from '../../components/Course'
  import PaymentPopup from '../../components/PaymentPopup'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import merge from 'webpack-merge'
  import {industryList} from '../../config/base'
  import 'swiper/dist/css/swiper.css'
  const list = [
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
    {imgSrc:'', name: '上汽集团'},
  ];
  const baseReq = {
    consultType: "1",  //选中的单选框，默认是企业
    companyName: "",
    industry: null,
    name: "",
    mobile: "",
    job: "",
    content: ""
  }
  export default {
    data() {
      return {
        swiperOption: {  //轮播配置
          navigation: {
            prevEl: '.swiper-btn-prev',
            nextEl: '.swiper-btn-next'
          },
          loop: false
        },
        navIndex: 0,
        businessList: this.filterArr(list), //服务企业列表
        currentSwiperIndex: 0, //当前轮播下标
        lecturerList:[],
        requestParams: JSON.parse(JSON.stringify(baseReq)), //请求参数

        industryList,

        showPayPopup: false, // 购买二维码
        payInfo: {}
      }
    },
    computed: {
      ...mapState({
        teacher: state => state.teachers.teacherList,
        courseList: state => state.course.excellentCourseList
      }),
    },
    created() {
      document.title = `一站式企业内训服务商 - 插座学院`
      // 获取讲师列表
      this.getTeacher();
      // 获取精品课课程
      this.getCourseList();
    },
    methods: {
      ...mapMutations({
        changeCourse: 'course/CHANGE_COURSE'
      }),
      ...mapActions({
        getTeacherList: 'teachers/getTeachersList',
        getCourseList: 'course/getCourseList',
        saveCompanyInfo: 'course/saveCompanyInfo'
      }),
      /**
       * 获取讲师
       *
       * @returns 讲师列表
       */
      async getTeacher() {
        const {page, desc, category_id, keyword} = this.$route.query;

        await this.getTeacherList({
          page,
          desc,
          keyword,
          category_id
        });
      },


      /**
       * 切换页码
       * @page 页码
       */
      async changePage(page) {
        this.$router.replace({
          query: merge(this.$route.query, {
            page
          })
        });
        this.getTeacher();
      },

      /**
       * 切换讲师排序方式
       * @desc 排序方式 created_at || browse
       * @index 排序索引
       */
      async changeArticleDesc(desc, index) {
        this.$router.replace({
          query: merge(this.$route.query, {
            desc
          })
        });
        this.navIndex = index;
        this.getTeacher();
      },

      /**swiper滑动 */
      slideChange(){
        this.currentSwiperIndex = this.$refs.mySwiper.swiper.activeIndex;
      },

      /***校验请求的数据 */
      validateRequest(){
        const {
          companyName,
          industry,
          name,
          mobile,
          job,
          content
        } = this.requestParams;

        /**数据为空的验证 */
        if(!companyName.trim()){
          this.$message({
            message: '请填写企业名称',
            type: 'warning'
          });
          this.$refs.companyName.focus();
          return false
        }
        if(!industry){
          this.$message({
            message: '请选择行业',
            type: 'warning'
          });
          return false
        }
        if(!name.trim()){
          this.$message({
            message: '请填写姓名',
            type: 'warning'
          });
          this.$refs.name.focus();
          return false
        }
        if(!mobile.trim()){
          this.$message({
            message: '请填写手机号',
            type: 'warning'
          });
          this.$refs.mobile.focus();
          return false
        }
        if(!job.trim()){
          this.$message({
            message: '请填写职位名称',
            type: 'warning'
          });
          this.$refs.job.focus();
          return false
        }

        return true;
      },
      /**提交数据 */
      async submitData(){
        const status = this.validateRequest();
        if(!status){
          return;
        }
        const baseInfo = {
          employeeCount: "",
          duty: "",
          peopleNum: 0,
          chennel: "",
          type: 4,
          from: 'APP'
        }
        const {data} = await this.saveCompanyInfo({...baseInfo, ...this.requestParams});
        if(data.status === "200"){
          this.requestParams = baseReq;
          this.$message({
            message: '信息填写成功',
            type: 'success'
          });
        }
      },
        /**企业案例数组转化 */
      filterArr(list){
        const index = list.length / 6 > 0 ? Math.ceil(list.length / 6) : list.length / 6;
        let newList= [];
        for(let i=0;i<index;i++){
            newList.push(list.splice(0, 6));
        }
        return newList;
      },

      /**
       * 路由跳转
       * @param path 路由地址
       */
      toPath(path) {
        let token = this.$ls.get("CZXY_TOKEN");
        if (token) {
          this.$router.push(path);
        }else{
          this.$router.push('/login');
        }
      },

      /**跳转到讲师详情 */
      seeTeacherDetail(item){
        this.toPath('/training/lecturer/'+ item.id);
      },

      /**跳转到课程详情 */
      seeCourseDetail(item){
        this.toPath('/training/courses/'+ item.teacher.teacher_id);
      },

      /**修改感兴趣状态 */
      changeCollectStatus(item){
        let list = this.courseList.data.list;

        const order = list.findIndex(course => course.id === item.id);
        list.splice(order, 1, item);
        this.changeCourse(list);

      },

      /**
       * 购买
       */
      changeHandleReport(item){
        this.showPayPopup = true
        this.courseId = item.pay_id
        this.from = ''
      }
    },
    components: {
      swiper,
      swiperSlide,
      Lecturer,
      Course,
      PaymentPopup
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .container {
    width: 85%;
    margin: 0 auto;
    overflow: hidden;
    min-height: 100vh;
    background-color: #FFFFFF;
    margin-bottom: -295px;
    &:after {
      content: "";
      display: block;
      width: 100%;
      height: 380px;
    }

    // 大图导航栏
    .bgPicture{
      padding: 48px 0;
      box-sizing: border-box;
      width: 100%;
      background-image: linear-gradient(to bottom right, #3E589CFF, #1D2C63FF);
      .introduce{
        color: #ddd;
        .title{
          color: #fff;
          margin-top: 50px;
          font-size: 60px;
        }
        .desc{
          margin: 15px 24px 60px 0;
          font-size: 22px;
        }
        .speciality{
          margin-left: 16px;
          li{
            font-size: 20px;
            line-height: 2;
            position: relative;
            &:before{
              content: "";
              position: absolute;
              left: -16px;
              top: 50%;
              transform: translate(0, -3px);
              width: 6px;
              height: 6px;
              background: #ddd;
              border-radius: 50%;
            }
          }
        }
      }
      .contact{
        color: #fff;
        text-align: center;
        min-height: 400px;
        .limit{
          margin-top: 100px;
          font-size: 24px;
        }
        .phone{
          font-size: 50px;
          margin: 12px 0 46px;
        }
        .findBusiness{
          width: 258px;
          height: 54px;
          border: none;
          outline: 0;
          background: #fff;
          color: #06609A;
          font-size: 22px;
          border-radius: 27px;
          cursor: pointer;
        }
      }
    }

    // 合作企业
    .cooperation{
      background: #F7F7F7;
      padding-bottom: 70px;
      h3{
        padding: 70px 24px 60px;
        text-align: center;
        font-size: 30px;
        color: #333333;
        .sign{
          color: #06609A;
          font-size: 40px;
          padding: 0 6px;
        }
      }
      .swiperAuto{
        position: relative;
        .swiperCon{
            margin: 0 70px;
            .slide-box{
                padding: 0 7.5px;
                .company{
                    padding: 10px 20px;
                    div{
                        background: #fff;
                        height: 110px;
                        border-radius: 6px;
                        color: #123061;
                        .icon{
                            width: 50px;
                            height: 50px;
                            background: #ccc;
                            vertical-align: middle;
                            margin-right: 12px;
                        }
                        .name{
                            font-size: 20px;
                        }
                    }

                }
            }
        }
        .swiper-btn-prev{
            display: block;
            position: absolute;
            left: 0px;
            top: 50%;
            transform: translate(0, -29px);
            width: 58px;
            line-height: 58px;
            background: #f1f1f1;
            border-radius: 50%;
            text-align: center;
            color: #06609A;
            outline: 0;
            cursor: pointer;
            span{
                font-size: 20px;
            }
        }
        .swiper-btn-next{
            display: block;
            position: absolute;
            right: 0px;
            top: 50%;
            transform: translate(0, -29px);
            width: 58px;
            line-height: 58px;
            background: #f1f1f1;
            border-radius: 50%;
            text-align: center;
            color: #06609A;
            outline: 0;
            cursor: pointer;
            span{
                font-size: 20px;
            }
        }
        .btn-disabled{
            display: none;
        }
      }
    }
    // 内训讲师
    .mode{
      padding: 70px 80px 50px;
      .top{
        .title{
          font-size: 46px;
          color: #333;
          .subTitle{
            font-weight: normal;
            font-size: 24px;
            color: #666;
            margin-left: 16px;
          }
        }
        .more{
          font-size: 24px;
          color: #06609A;
          cursor: pointer;
        }

      }
    }
    .teachers{
      color: #fff;
    }
    // 线下课
    .classes{
      background: #f7f7f7;
    }

    // 提交信息
    .subInfo{
      text-align: center;
      margin: 80px 24px 0;
      color: #333;
      .tips{
        font-size: 30px;
        margin-bottom: 40px;
      }
      .selectRadio{
        margin-bottom: 50px;
        .radioName{
          font-size: 24px;
        }
      }

      .competeInfo{
        margin: 0 32px;
        text-align: left;
        .industry{
          margin: 15px 20px;
        }
        input{
          color: #666;
          margin: 15px 20px;
          height: 64px;
          border-radius: 32px;
          background: #f7f7f7;
          font-size: 24px;
          padding-left: 30px;
          outline: 0;
          border: 0;
        }

        .courseNeed{
          color: #666;
          font-size: 24px;
          line-height: 2;
          border-radius: 10px;
          background: #f7f7f7;
          padding: 20px 0 0 30px;
          outline: 0;
          border: 0;
          margin: 15px 20px;
        }
      }

      .submitInfo{
        cursor: pointer;
        margin-top: 60px;
        width: 246px;
        background: #06609A;
        border-radius: 34px;
        line-height: 68px;
        text-align: center;
        color: #fff;
        font-size: 30px;
        outline: 0;
        border: 0;
      }
    }

    .Modular{
      height: 152px;
    }

    .getGift{
      position: fixed;
      left: 0;
      bottom: 0;
      z-index: 10;
      width: 100%;
      height: 100px;
      background: #EDCBB2;
      padding: 0 54px;
      color: #333;
      box-sizing: border-box;
      .giftPic{
        width: 106px;
        display: block;
      }
      .sign{
        margin-left: 16px;

        p{
          font-size: 36px;
          margin-bottom: 6px;
        }
        span{
          font-size: 16px;
        }
      }
      .haveGift{
        font-size: 20px;
        margin-right: 24px;
        span{
          font-size: 36px;
          color: #06609A;
        }
      }
      .freeGet{
        cursor: pointer;
        width: 250px;
        background: #06609A;
        border-radius: 32px;
        line-height: 64px;
        text-align: center;
        color: #fff;
        font-size: 22px;
        outline: 0;
        border: 0;
      }
    }
  }

  .page {
    padding: 32px 0;
    text-align: center;
  }

    /**修改原组件的样式 */
    .selectRadio /deep/ .el-radio__input.is-checked .el-radio__inner {
        border-color: #06609A;
        background: #06609A;
    }

    .selectRadio /deep/ .el-radio__input.is-checked+.el-radio__label{
        color: #06609A;
    }
    .industry /deep/ .el-input .el-input__inner{
      background: #f7f7f7;
      font-size: 24px;
      color: #666;
      border: none;
    }

  @media screen and (min-width: 200px) and (max-width: 768px) {
    .container {
      box-sizing: border-box;
      display: flex;
      width: 100%;
      overflow: hidden;
      flex-direction: column;
    }

    .sidebar {
      box-sizing: border-box;
      width: auto;
      flex: 1;
      margin-left: 0;
      margin-top: 24px;
    }
  }
</style>



