<template>
    <div 
        class="character" 
        @mouseenter="changeMoveStatus(true)" 
        @mouseleave="changeMoveStatus(false)"
    >
        <p class="photoShow" :style='{backgroundImage:"url("+ teacherInfo.list_cover +")"}'></p>
        <div class="desc">
            <p class="name">
                {{teacherInfo.name}}
                <span v-if="teacherInfo.catalog_type_name">{{teacherInfo.catalog_type_name}}</span></p>
            <div class="ellipsis2 info">
                <p>{{teacherInfo.bewrite}}</p>
            </div>
        </div>
        <!-- v-if="isMove"-->
        <div class="desc descHover" v-if="isMove">
            <div class="outer" ref="outer">
                <div class="top" ref="top">
                    <p class="name">
                        {{teacherInfo.name}}
                        <span v-if="teacherInfo.catalog_type_name">{{teacherInfo.catalog_type_name}}</span>
                    </p>
                    <div class="info">
                        <p>{{teacherInfo.bewrite}}</p>
                        <p class="goodAt">{{teacherInfo.info}}</p>
                    </div> 
                </div>
            </div>
            <span class="details">查看详情<span class="iconfont next"></span></span>
        </div>
    </div> 
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import merge from 'webpack-merge'

  export default {
    data() {
      return {
          isMove: false
      }
    },
    props: {
        teacherInfo: {
            type: Object,
            default: {}
        }
    },
    mounted() {
        // 兼容描述字数太多的情况
        //this.showEllipsis();
    },
    computed: {
        ...mapState({})
    },
    methods: {
      ...mapActions({
        showUserManager: 'user/showUserManager',
        getArticleList: 'article/getArticleList'
      }),

        // 显示省略号
        showEllipsis(){
            const outer = this.$refs.outer, 
                top = this.$refs.top;
            // let words = top.innerHTML.split(/(?<=[\u4e00-\u9fa5])|(?<=\w*?\b)/gi);
            console.log(top.innerHTML);
            while (top.scrollHeight > outer.clientHeight) {
                top.innerHTML = words.join('') + '...';
            }
        },
      /**
       * 切换导航栏
       */
      changeNav(path, index) {
        this.$router.replace({
          query: merge({})
        });
        this.navIndex = index;
        this.toPath(path);
      },

        changeMoveStatus(status){
            this.isMove = status;
        }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
.character{
    margin-top: 50px;
    // height: 478px;
    width: 100%;
    box-shadow: 3px 4px 8px #999;
    position: relative;
    overflow: hidden;
    border-radius: 6px;
    cursor: pointer;
    .photoShow{
        width: 100%;
        height: 254px;
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
    }
    .desc{
        width: 100%;
        background:#fff;
        padding: 24px;
        box-sizing: border-box;
        .name{
            font-size: 24px;
            color: #06609A;
            span{
                padding: 4px 6px;
                margin-left: 8px;
                background: #E2AD87;
                color: #80624C;
                border-radius: 4px;
                font-size: 12px;
            }
        }
        .info{
            font-size: 14px;
            color: #666;
            line-height: 2;
            margin-top: 20px;
            .goodAt{
                margin-top: 10px;
            }
        }
    }
    .descHover{
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 0;
        padding: 42px 20px 0;
        animation: mymove 0.3s ease-out 0s;
        height: 100%;
        opacity: 1;
        background-image: linear-gradient(to bottom left, #E1E9F5, #4F6585);
        .outer{
            height: calc(100% - 80px);
            overflow: hidden;
            .top{
                box-sizing: border-box;
                .name{
                    color: #fff;
                    span{
                        color: #fff;
                    }
                }
                .info{
                    color: #fff;
                }
            }
        }
        
        .details{
            position: absolute;
            left: 0;
            bottom: 0;
            display: block;
            width: 100%;
            height: 54px;
            line-height: 54px;
            text-align: center;
            font-size: 20px;
            color: #fff;
            background: #E2AD87;
            .iconfont{
                margin-left: 8px;
            }
        }
    }
}


/*定义动画*/
@keyframes mymove {
    from {   /*0%*/
        opacity: 0;
    }
    to {    /*100%*/
        opacity: 1;
    }
}

.spill{
	position: relative; 
} 
.spill::after{
	content: "..."; 
	position: absolute; 
	bottom: 0; 
	right: 0; 
	padding-left: 30px; 
}

</style>



